.page-title {
    margin: unset;
    @media screen and (min-width: $large_screen) {
        margin: unset;
        margin-bottom: 1rem;
    }
}

.excerpt-title,
.entry-title {
    margin-right: 1rem;
    @media screen and (min-width: $large_screen) {
        margin: unset;
        margin-bottom: 1rem;
    }
    a {
        font-weight: bold;
        color: $light_orange;
    }
    a:hover {
        background:$mid_orange; 
        background: -webkit-linear-gradient(to right, $mid_orange, $darker_orange);
        background: linear-gradient(to right, $mid_orange, $darker_orange);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;  
        text-decoration: underline;
    }
}

.excerpt-meta,
.entry-meta {
    color:$light_gray;
    padding: 0.5rem 0;
    font-size: 0.8rem;
    @media screen and (min-width: $extra_large_screen) {
        padding: unset;
        font-size: 0.9em;
    }     
}

.thumb-container{
    height: 350px;
    overflow: hidden;
    @media screen and (min-width: $large_screen) {
        height: 400px;
    }
    img {
        object-fit: cover;
        position:fixed;
        z-index: -10000;
        height: 350px;
        width:100vw;
        filter: grayscale(0.7);
        @media screen and (min-width: $large_screen) {
         filter: blur(1px);
         margin: 0;  
         height: 500px;
         margin-top: 20px;
         margin-bottom:30px;
        }
    }                
}   


.single-article {
    margin-top: unset;
}

.page-content {
    padding-left: 2rem;
}

.updated:not(.published) {
	display: none;
}

.page-header {
    min-width: 250px;
    max-width: 900px;
    margin: 0 auto;
    margin-top: 4em;
    font-size: 0.8rem;
    padding-left: 2rem;
    border-left: 1px solid #111;
    @media screen and (min-width: $large_screen) {
        margin-top: 6em;
        font-size: 1rem;
    }  
}

.site-info {
    font-size: 0.9rem;
}

.margin-top {
    margin-top: 50px; 
    @media screen and (min-width: $large_screen) {
        margin-top: 120px;
    }   
}

.margin-2 {
    margin: 0 1rem;
    @media screen and (min-width: $large_screen) {
        margin: 0 2rem;
    } 
}

.p-1 {
    padding-top:1rem;
}

.white-bg {
    background-color: $light_color;
}

.cat-title {
    font-size: 1.6em;
}

.custom-logo {
    height: 50px;
    width: 50px;
    margin-top: 0.5em;
    margin-left: 10px;
    transition: all 0.2s ease-out;
    @media screen and (min-width: $large_screen) {
        height: 80px;
        width: 80px;
    }   
}

.no-results-container {
    min-height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}
