/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #413742;
	font-family: "Montserrat", sans-serif;
}

/**
* Type sizes
*/
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Montserrat", sans-serif;
	line-height: unset;
	color: #413742;
	line-height: 1.8rem;
	font-weight: normal;
}

h1 {
	font-size: 1.4641rem;
}

h2,
.search-title {
	font-size: 1.331rem;
}

h3 {
	font-size: 1.21rem;
}

h4 {
	font-size: 1.1rem;
}

h5 {
	font-size: 1rem;
}

p {
	font-family: "Playfair Display", serif;
	font-size: 1rem;
	line-height: 1.5rem;
	color: #413742;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	font-family: "Courier 10 Pitch", courier, monospace;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 0.9375rem;
}

abbr,
acronym {
	border-bottom: 1px dotted #CC1F38;
	cursor: help;
}

mark,
ins {
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Animations
--------------------------------------------------------------*/
@keyframes slideInFromLeft {
	0% {
		transform: translateX(20%);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes slideInFromRight {
	0% {
		transform: translateX(-20%);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

.excerpt:first-of-type .post-thumbnail:first-of-type {
	animation: 0.3s ease-out 0.6s 1 slideInFromRight;
	animation-fill-mode: both;
}

.excerpt:first-of-type .excerpt-title:first-of-type {
	animation: 0.5s ease-out 0.1s 1 slideInFromLeft;
	animation-fill-mode: both;
}

.excerpt:first-of-type .excerpt-meta:first-of-type {
	animation: 0.5s ease-out 0.2s 1 slideInFromLeft;
	animation-fill-mode: both;
}

.excerpt:first-of-type .excerpt-content:first-of-type {
	animation: 0.5s ease-out 0.3s 1 slideInFromLeft;
	animation-fill-mode: both;
}

.excerpt:first-of-type .read-more:first-of-type {
	animation: 0.5s ease-out 0.4s 1 slideInFromLeft;
	animation-fill-mode: both;
}

.excerpt:first-of-type .excerpt-header:first-of-type .cat-links:first-of-type {
	display: block;
	/* This section calls the slideInFromLeft animation we defined above */
	animation: 0.5s ease-out 0s 1 slideInFromLeft;
	animation-fill-mode: both;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
/* Inherit box-sizing to more easily change it's value on a component level.
@link http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	overflow-x: hidden;
	scroll-behavior: smooth;
}

body {
	overflow-x: hidden;
}

hr {
	background-color: #F7F7F8;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	max-width: 100%;
	margin-top: 1.2em;
}

.wp-block-cover {
	max-width: initial;
	max-height: 300px;
}

figure {
	margin: 1em 0;
}

.read-more {
	display: none;
}

.card:first-of-type .read-more {
	display: block;
	color: #7B6A7F;
	background: #F7F7F8;
	border: 1px solid #7B6A7F;
	height: 45px;
	width: 200px;
	cursor: pointer;
	margin-top: 20px;
	box-shadow: none;
	padding-bottom: 3px;
	font-family: "Playfair Display", serif;
	transform: all 0.2s ease;
	margin-bottom: 1rem;
}

@media screen and (min-width: 1600px) {
	.card:first-of-type .read-more {
		margin-top: 50px;
		height: 50px;
	}
}

.card:first-of-type .read-more:active {
	transform: scale(0.99);
}

@media screen and (min-width: 1023px) {
	.card:first-of-type .read-more:hover {
		background: #B62626;
		background: linear-gradient(224.35deg, #B62626 20.34%, #EE3626 73.72%);
		color: #F7F7F8;
		border: none;
		box-shadow: unset;
	}
}

.grecaptcha-badge {
	opacity: 0;
	visibility: hidden;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: none;
	background: #B62626;
	background: linear-gradient(224.35deg, #B62626 20.34%, #EE3626 73.72%);
	color: #F7F7F8;
	height: 45px;
	width: 200px;
	cursor: pointer;
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
	transition: all 0.2s ease-in;
	font-size: 1rem;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	box-shadow: 0 1px 17px #273746;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #CC1F38;
	border: 1px solid #CC1F38;
	border-radius: 3px;
	padding: 3px;
	width: 100%;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #7B6A7F;
}

select {
	border: 1px solid #CC1F38;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #CC1F38;
	text-decoration: none;
	transition: all 0.2s ease-out;
}

a:visited {
	color: #CC1F38;
}

a:hover, a:focus, a:active {
	color: #B62626;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

.nav-previous p,
.nav-next p {
	color: #CC1F38;
	font-size: 0.8em;
}

.nav-title {
	font-family: "Playfair Display", serif;
}

.nav-links {
	display: flex;
	justify-content: center;
	line-height: 1rem;
	margin: 1.3rem;
}

.nav-links .page-numbers {
	display: block;
	padding: 0.6rem;
}

.nav-links .current {
	border: 1px solid #CC1F38;
	color: #CC1F38;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
	padding: 1rem;
}

.tags-links {
	font-size: 0.9rem;
}

.edit-link {
	font-size: 0.8  rem;
	text-transform: uppercase;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.menu {
	color: #413742;
	margin: 0;
	padding-left: 0;
	list-style-type: none;
	letter-spacing: 0.1rem;
	display: flex;
	flex-direction: column;
}

@media screen and (min-width: 1420px) {
	.menu {
		flex-direction: row;
		height: unset;
		position: static;
		background-color: unset;
		justify-content: flex-end;
		min-width: 500px;
	}
}

.menu li {
	position: relative;
	padding: 0.6rem 0;
}

@media all and (orientation: landscape) {
	.menu li {
		padding: 0.3rem 0;
	}
}

@media all and (min-width: 1023px) and (orientation: landscape) {
	.menu li {
		padding: 0.6rem 0;
	}
}

.menu li a {
	display: block;
	color: #EE3626;
	font-size: 14px;
}

@media screen and (min-width: 1420px) {
	.menu li a {
		box-sizing: border-box;
		color: #F7F7F8;
		border-bottom: 1px solid rgba(1, 1, 1, 0);
		padding: 0.4em 0.6em;
		border-width: 1px;
		transition: all 0.3s ease-in;
	}
}

.main-menu {
	position: absolute;
	left: 0px;
	top: 0px;
	display: flex;
	flex-direction: column;
	height: 100vh;
	width: 100vw;
	background: #F7F7F8;
	justify-content: center;
	align-items: center;
	transform: translateX(-101vw);
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
	transition: transform 0.2s ease-in;
}

@media screen and (min-width: 1420px) {
	.main-menu {
		box-shadow: unset;
		position: static;
		flex-direction: row;
		height: unset;
		width: unset;
		background: unset;
		min-width: 350px;
		transition: unset;
		transform: translateX(0px);
		top: 0;
	}
}

.menu-active {
	display: flex;
	transform: translateX(0px);
}

@media screen and (min-width: 1420px) {
	.menu-active {
		position: unset;
	}
}

.posts-navigation,
.post-navigation {
	max-width: 900px;
	margin: 3em auto;
}

.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
	margin: 0 auto;
	font-size: 1rem;
}

@media screen and (min-width: 1023px) {
	.posts-navigation .nav-links,
	.post-navigation .nav-links {
		font-size: 1.2rem;
	}
}

@media screen and (min-width: 1600px) {
	.posts-navigation .nav-links,
	.post-navigation .nav-links {
		font-size: 1.3rem;
	}
}

.toggler {
	padding: auto 10px;
	display: flex;
	flex-direction: column;
	margin: 5px 5px;
	cursor: pointer;
}

@media screen and (min-width: 1420px) {
	.toggler {
		display: none;
	}
}

.toggler div {
	height: 2px;
	width: 30px;
	margin: 4px 0;
	background-color: #413742;
}

@media screen and (max-width: 1420px) {
	.toggler div {
		width: 23px;
		margin: 3px 0;
	}
}

.nav-menu {
	display: flex;
}

.sub-menu {
	display: none;
}

.menu-hider {
	z-index: -10000000000;
	display: none;
}

.menu-hider-active {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}

/**
*Menu stagered animnation 
*/
.open-menu-items li {
	animation: 0.4s ease pulse;
	animation-fill-mode: both;
}

.open-menu-items li:nth-child(1) {
	animation-delay: 0.06667s;
}

.open-menu-items li:nth-child(2) {
	animation-delay: 0.13333s;
}

.open-menu-items li:nth-child(3) {
	animation-delay: 0.2s;
}

.open-menu-items li:nth-child(4) {
	animation-delay: 0.26667s;
}

.open-menu-items li:nth-child(5) {
	animation-delay: 0.33333s;
}

.open-menu-items li:nth-child(6) {
	animation-delay: 0.4s;
}

.open-menu-items li:nth-child(7) {
	animation-delay: 0.46667s;
}

.open-menu-items li:nth-child(8) {
	animation-delay: 0.53333s;
}

.open-menu-items li:nth-child(9) {
	animation-delay: 0.6s;
}

@keyframes pulse {
	0% {
		transform: translateX(-75px);
	}
	100% {
		ttransform: translateX(0px);
	}
}

/*--------------------------------------------------------------
## Search form
--------------------------------------------------------------*/
.widget_search {
	left: 0px;
	top: 0px;
	position: fixed;
	width: 100%;
	list-style: none;
	height: 100vh;
	margin: 0;
	padding-left: 0;
	background-color: #F7F7F8;
	transform: translateY(-200px);
	opacity: 0;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
	transition: all 0.3s ease-in;
	z-index: -150000;
	flex-wrap: wrap;
	transform: translateY(0px);
	align-content: center;
	margin-bottom: 2rem;
}

@media screen and (min-width: 1023px) {
	.widget_search {
		display: flex;
		height: 100px;
		width: 100vw;
		justify-content: center;
		align-content: center;
	}
}

.search-active {
	z-index: 150000;
	opacity: 1;
}

@media screen and (min-width: 1023px) {
	.search-active {
		transform: translateY(100px);
	}
}

.search-active-scrolled {
	z-index: 150000;
	transform: translateY(0px);
	opacity: 1;
}

@media screen and (min-width: 576px) {
	.search-active-scrolled {
		transform: translateY(70px);
	}
}

@media screen and (min-width: 1023px) {
	.search-active-scrolled {
		transform: translateY(80px);
	}
}

@media screen and (min-width: 1023px) {
	.menu-scroller {
		transform: translateY(80px);
	}
}

@media screen and (min-width: 1023px) {
	.menu-scroller2 {
		transform: translateY(100px);
	}
}

.search-form {
	margin: auto 0;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 2px;
}

@media screen and (max-width: 425px) {
	.search-form {
		display: block;
	}
}

@media screen and (min-width: 1023px) {
	.search-form {
		width: unset;
	}
}

@media screen and (min-width: 1023px) {
	.search-form label input[type="search"] {
		width: 600px;
	}
}

.search-field {
	font-size: 1em;
	height: 45px;
	font-family: "Montserrat", sans-serif;
	flex-grow: 3;
	color: #F7F7F8;
	border-radius: 0;
	text-align: center;
}

@media screen and (max-width: 425px) {
	.search-field {
		height: 35px;
		width: 100%;
	}
}

.search-form input[type="submit"] {
	background: #B62626;
	background: linear-gradient(224.35deg, #B62626 20.34%, #EE3626 73.72%);
	color: #F7F7F8;
	border-radius: 0;
	width: 100%;
	height: 35px;
